import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AzureService } from '../../services/azure.service';
import 'rxjs/add/operator/finally';
import { MixPanelService } from '../../services/mix-panel.service';
import * as moment from 'moment';

@Component({
  selector: 'app-confirmed-link',
  templateUrl: './confirmed-link.component.html',
  styleUrls: ['./confirmed-link.component.scss']
})
export class ConfirmedLinkComponent implements OnInit {
  autoConfirmation: boolean;

  isLoading: boolean;

  error: string;

  constructor(
    private activatedRoute: ActivatedRoute,
    private azureService: AzureService,
    private mixPanelService: MixPanelService
  ) {}

  ngOnInit() {
    this.isLoading = true;

    let response: any = {};

    this.activatedRoute.params
      .mergeMap(q => this.azureService.getConsultation(q.id))
      .map(val => {
        response = val;

        const date = moment(`${val.date._} ${val.time._}`, 'DD/MM/YYYY HH:mm');
        const diff = date.diff(moment(), 'h');

        if (diff < 13) {
          response.error = `Link de confirmacao clicado em ${diff} horas antes da consulta`;
          this.error = `Ol ${
            val.name._
          }, infelizmente o horrio escolhido no est mais disponvel :(`;
        }

        return response;
      })
      .map(() => {
        this.autoConfirmation = response.workPlaceAutoConfirmation
          ? response.workPlaceAutoConfirmation._
          : null;

        this.mixPanelService.onValidate(response);

        if (!response.error)
          this.autoConfirmation
            ? this.mixPanelService.onValidateAutoConfirmation()
            : this.mixPanelService.onValidateNoAutoConfirmation();
      })
      .filter(() => {
        const shouldValidate = response.stepNumber._ > -1 && response.stepNumber._ !== 5;

        return (this.isLoading = shouldValidate);
      })
      .mergeMap(val =>
        this.azureService.setConfirmed({
          databaseID: response.RowKey,
          error: response.error
        })
      )
      .subscribe(
        () => {
          this.isLoading = false;
        },
        err => {
          this.error = 'Consulta no encontrada';
          this.isLoading = false;
        }
      );
  }
}



