import {Search} from './search';
import * as moment from 'moment';

export class User {

  constructor(public name?: string,
              public phone?: string,
              public speciality = {name: 'Selecione a Especialidade', id: 0},
              public coordinates?: { lat: number, lng: number },
              public schedule?: Search,
              public birthdate?: string,
              public who_will_consult = 'me',
              public locality ?: { state?: string, city?: string, neighborhood?: string },
              public databaseID?: string,
              public expiration?: moment.Moment,
              public isEmptyResult = false,
              public email?: string,
              public lastName?: string) {
  }
}



