import {Component, OnInit} from '@angular/core';
import {TrackingService} from '../../services/tracking.service';
import {ScheduleService} from '../../services/schedule.service';
import {User} from '../../models/user';
import {cloneDeep} from 'lodash';
import {AzureService} from '../../services/azure.service';

@Component({
  selector: 'app-finalized',
  templateUrl: './finalized.component.html',
  styleUrls: ['./finalized.component.scss']
})
export class FinalizedComponent implements OnInit {

  user: User;

  // isEmailSent = false;
  // isLoading = false;

  constructor(private trackingService: TrackingService,
              private scheduleService: ScheduleService) {
  }

  ngOnInit() {

    this.user = this.scheduleService.user;
    this.setUserToLocalStorage();
  }

  setUserToLocalStorage() {
    const storage = cloneDeep(this.user);
    this.user.databaseID = cloneDeep(this.scheduleService.databaseID);

    storage.databaseID = this.scheduleService.databaseID = storage.expiration = null;
    localStorage.setItem('user', JSON.stringify(storage));
  }

}



