import {Component, Input, OnInit} from '@angular/core';
import {ModalDismissReasons, NgbActiveModal, NgbModal} from '@ng-bootstrap/ng-bootstrap';
import * as moment from 'moment';
import {Router} from '@angular/router';
import {ScheduleService} from '../../services/schedule.service';
import {TrackingService} from '../../services/tracking.service';

@Component({
  selector: 'app-location-modal',
  templateUrl: './location-modal.component.html',
  styleUrls: ['./location-modal.component.scss']
})
export class LocationModalComponent implements OnInit {

  @Input() doctors;

  constructor(public activeModal: NgbActiveModal,
              private router: Router,
              private scheduleService: ScheduleService,
              private trackingService: TrackingService) {
  }

  ngOnInit() {
  }

  setDate() {
    const date = moment(`${this.doctors[0].date} ${this.doctors[0].availability.time}`, 'YYYY-MM-DD HH:mm');
    return `${date.format('DD')} de ${date.format('MMMM')} s ${this.doctors[0].availability.time}h`;
  }

  onSelected(d) {
    this.scheduleService.user.schedule = d;

    this.activeModal.close();

    this.trackingService.onAvailabilitySelected(this.scheduleService.user);

    this.router.navigate(['informacao']);

  }


}



