export class Masks {

  public static phoneMask() {
    return ['(', /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/];
  }

  public static  birthdateMask() {
    return [ /\d/, /\d/, '/',  /\d/, /\d/, '/', /\d/, /\d/, /\d/, /\d/];

  }
}



