import {Component, OnInit} from '@angular/core';
import {User} from '../../models/user';
import {ScheduleService} from '../../services/schedule.service';
import {Router} from '@angular/router';
import {Masks} from '../../utils/masks';
import * as moment from 'moment';
import {Utils} from '../../utils/utils';
import {ConfirmationModalComponent} from '../../components/confirmation-modal/confirmation-modal.component';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {TrackingService} from '../../services/tracking.service';
import createAutoCorrectedDatePipe from 'text-mask-addons/dist/createAutoCorrectedDatePipe';
import {AzureService} from '../../services/azure.service';

@Component({
  selector: 'app-confirmation',
  templateUrl: './confirmation.component.html',
  styleUrls: ['./confirmation.component.scss']
})
export class ConfirmationComponent implements OnInit {

  user: User;

  validation = false;

  userName: string;

  birthdateMask = Masks.birthdateMask();

  autoCorrectedDatePipe;

  confirming: boolean;

  fullName = '';

  constructor(private scheduleService: ScheduleService,
              private router: Router,
              private modalService: NgbModal,
              private  trackingService: TrackingService) {
  }

  ngOnInit() {

    this.autoCorrectedDatePipe = createAutoCorrectedDatePipe('dd/mm/yyyy');

    this.user = this.scheduleService.user;

    this.fullName = `${this.user.name} ${this.user.lastName}`;

    this.userName = this.user.name;
    this.user.who_will_consult = 'me';
  }

  onWhoWillConsult(event) {

    if (event.target.checked) {
      this.user.name = '';
      this.user.who_will_consult = 'other_person';

    } else {
      this.user.name = this.userName;
      this.user.who_will_consult = 'me';
    }
  }



  isBirthdateInvalid() {
    if (!this.user.birthdate || this.user.birthdate.length === 0)
      return true;

    else {
      const b = this.user.birthdate.replace(/\D+/g, '');

      return b.length !== 8;
    }
  }

  validateFields() {
    return this.fullName.length > 0 &&
      !this.isBirthdateInvalid();
  }

  onSubmit() {
    this.validation = true;

    if (this.validateFields()) {
      this.scheduleService.user = this.user;

      this.confirming = true;

      this.trackingService.onConfirmAppointment({...this.user, name: this.fullName})
        .subscribe((data) => {
            this.scheduleService.databaseID = data.id;
            this.openModal();

            this.confirming = false;
          },
          err => this.confirming = false);
    }
  }

  openModal() {
    this.modalService.open(ConfirmationModalComponent, {size: 'lg', backdrop: 'static'});
  }

}



