import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {AvailabilityComponent} from './pages/availability/availability.component';
import {ConfirmationComponent} from './pages/confirmation/confirmation.component';
import {RequestComponent} from './pages/request/request.component';
import {FinalizedComponent} from './pages/finalized/finalized.component';
import {NoResultsComponent} from './pages/no-results/no-results.component';
import {ReceiptComponent} from './pages/receipt/receipt.component';
import {AuthGuardService} from './services/auth-guard.service';
import {ConfirmedLinkComponent} from './pages/confirmed-link/confirmed-link.component';

const routes: Routes = [
  {
    path: '',
    pathMatch: 'full',
    component: RequestComponent
  },
  {
    path: 'disponibilidade' ,
    component: AvailabilityComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: 'informacao',
    component: ConfirmationComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: 'confirmacao/:id',
    component: ConfirmedLinkComponent
  },
  {
    path: 'obrigado',
    component: FinalizedComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: 'sem-resultados',
    component: NoResultsComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: 'consulta/:id',
    component: ReceiptComponent
  },
  {
    path: '**',
    component: RequestComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}



