import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {User} from '../../models/user';
import {ApiService} from '../../services/api.service';
import 'rxjs/add/operator/mergeMap';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/reduce';

import {ScheduleService} from '../../services/schedule.service';
import * as moment from 'moment';

import 'moment/locale/pt-br';
import {Search} from '../../models/search';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {LocationModalComponent} from '../../components/location-modal/location-modal.component';
import {Utils} from '../../utils/utils';
import {TrackingService} from '../../services/tracking.service';
import {MixPanelService} from '../../services/mix-panel.service';
import {Log} from 'ng2-logger';

@Component({
  selector: 'app-availability',
  templateUrl: './availability.component.html',
  styleUrls: ['./availability.component.scss']
})
export class AvailabilityComponent implements OnInit {

  user: User;

  doctors;

  log = Log.create('AvailabilityComponent');

  availabilityAmount = [5, 5, 5];

  availableOnDate;

  isFromSP = false;

  dates = [moment().add(1, 'd'), moment().add(2, 'd'), moment().add(3, 'd')];

  constructor(private router: Router,
              private apiService: ApiService,
              private scheduleService: ScheduleService,
              private trackingService: TrackingService,
              private mixPanelService: MixPanelService,
              private modalService: NgbModal) {
  }


  ngOnInit() {
    this.user = this.scheduleService.user;
    this.doctors = this.scheduleService.searchResults;

    this.isFromSP = this.user.locality.state.localeCompare('SP') === 0;

    localStorage.setItem('user', JSON.stringify(this.user));

    this.trackingService.onRequestAppointment(this.user)
      .map((id) => this.user.databaseID = this.scheduleService.databaseID)
      .mergeMap(() => this.getAvailabilities())
      .subscribe((resp) => this.setAvailabilities(resp));
  }

  init() {
    this.doctors = this.availableOnDate = null;
  }

  openModal(time: string, index: number) {
    const modalRef = this.modalService.open(LocationModalComponent, {size: 'lg'});

    modalRef.componentInstance.doctors = this.sortFromSmallerPrice(time, index);

    this.scheduleService.user.schedule = modalRef.componentInstance.doctors[0];
    this.trackingService.onTimeSelected(this.scheduleService.user);
  }

  sortFromSmallerPrice(time: string, index: number) {
    return this.doctors[index]
      .filter(d => d.availability.time === time)
      .sort((a, b) => a.price_decimal - b.price_decimal);
  }

  getSmallerPrice(time: string, index: number) {
    return this.sortFromSmallerPrice(time, index)[0].price;
  }

  isPrevPossible() {
    return moment().add(1, 'd').isSameOrAfter(this.dates[0]);
  }

  formatWeekDay(date) {
    return Utils.formatWeekDay(date);
  }

  onNextorPrevDates(daysFromNow: number) {
    const d = this.dates[0].add(daysFromNow, 'd');
    this.dates = [d, this.dates[1].add(daysFromNow, 'd'), this.dates[2].add(daysFromNow, 'd')];

    this.getAvailabilities(d.format('YYYY-MM-DD'))
      .subscribe((resp) => this.setAvailabilities(resp));
  }


  onNextAvailability() {
    const d = moment(this.availableOnDate, 'DD/MM/YYYY');

    this.availableOnDate = null;

    this.dates = [d, moment(d).add(1, 'd'), moment(d).add(2, 'd')];

    this.getAvailabilities(d.format('YYYY-MM-DD'))
      .subscribe((resp) => this.setAvailabilities(resp));
  }

  getAvailabilities(date?) {
    this.init();

    return this.scheduleService.getAvailabilities(this.user.speciality.id, this.user.coordinates.lat, this.user.coordinates.lng, date)
  }

  private isResultEmpty() {
    return (this.doctors[0].length === 0 && this.doctors[1].length === 0 && this.doctors[2].length === 0) && !this.availableOnDate;
  }

  private setAvailabilities(resp) {

    this.scheduleService.searchResults = this.doctors = this.setAvailabilitiesArray(resp);


    if (this.isResultEmpty()) {
      this.trackingService.OnEmptyResult(this.user);
      this.router.navigate(['sem-resultados'], {skipLocationChange: true});
    }
    else
      this.mixPanelService.onAvailability(this.user);
  }

  setAvailabilitiesArray(response) {
    const perDay = [];

    this.log.info('Response', response);

    response
      .filter(doctor => doctor.schedules && doctor.schedules.length > 0)
      .forEach(doctor => {

        doctor.schedules
          .filter(schedule => this.dates.filter(d => d.isSame(moment(schedule.date), 'd')).length > 0)
          .forEach(schedule => {

            schedule.availabilities
              .forEach(availability => {
                const index = schedule.date;

                if (typeof perDay[index] !== 'object')
                  perDay[index] = [];

                perDay[index].push(
                  new Search(
                    index,
                    doctor.price,
                    doctor.price_decimal,
                    doctor.name,
                    doctor.address,
                    availability,
                    doctor.avatar,
                    doctor.auto_confirmation || false,
                    doctor.service_location_profile
                  ));
              });
          });
      });

    if (Object.keys(perDay).length === 0 && response.length > 0)
      this.checkCloserAvailability(response);

    return this.sortAvailabilities(perDay);
  }


  private checkCloserAvailability(response) {
    const thirtyDays = moment().add(30, 'd').format('YYYY-MM-DD');

    const val = response.map(c => {

      return c.schedules.reduce((acc, sch) => {
        return sch.date.localeCompare(acc) < 0 ? sch.date : acc;
      }, thirtyDays);
    })[0];

    if (thirtyDays.localeCompare(val) !== 0)
      this.availableOnDate = moment(val).format('DD/MM/YYYY');
  }


  private sortAvailabilities(perDay) {
    const arr = [];

    this.dates.forEach(d => {

      const date = d.format('YYYY-MM-DD');
      const temp = perDay[date];

      if (temp) {
        const i = arr.push(temp);

        arr[i - 1] = arr[i - 1]
          .sort((a, b) => a.availability.time.localeCompare(b.availability.time))
          .filter(a => this.checkBusinessRuleAvailability(date, a.availability.time));
      }
      else
        arr.push([]);
    });
    return arr;
  }


  private checkBusinessRuleAvailability(date, time) {

    const now = moment();
    const comparedDate = moment(date + ' ' + time, 'YYYY-MM-DD HH:mm');


    return now.isAfter(moment().hour(15).minute(0), 's') &&
      comparedDate.isAfter(moment().add('1', 'd').hour(13).minute(0), 's') || now.isSameOrBefore(moment().hour(15).minute(0), 's');
  }


}



