import {Component, OnInit} from '@angular/core';
import {AzureService} from '../../services/azure.service';
import {ActivatedRoute} from '@angular/router';
import {Consultation} from '../../models/consultation';
import * as moment from 'moment';
import 'moment/locale/pt-br';
import {} from '@types/googlemaps';
import {Log} from 'ng2-logger';

@Component({
  selector: 'app-receipt',
  templateUrl: './receipt.component.html',
  styleUrls: ['./receipt.component.scss']
})
export class ReceiptComponent implements OnInit {
  static coord;

  errorMsg: string;

  consultation: Consultation;

  log = Log.create('ReceiptComponent');

  constructor(private azureService: AzureService,
              private activatedRoute: ActivatedRoute) {
  }

  isGoogleUp() {
    window['__onGoogleLoaded'] = (ev) => {
      this.log.info('google maps api loaded');
    };
  }


  ngOnInit() {
    this.isGoogleUp();
    this.errorMsg = null;


    this.activatedRoute.params
      .mergeMap((q) => this.azureService.getConsultation(q.id))
      .subscribe(val => {

          this.consultation = new Consultation(
            val.name._,
            val.Timestamp._,
            val.avatar._,
            val.birthdate._,
            val.state._,
            val.city._,
            val.neighborhood._,
            val.date._,
            val.workPlaceLat._,
            val.workPlaceLng._,
            val.phone._,
            val.price._,
            val.speciality._,
            val.time._,
            val.workPlace._,
            val.workPlaceAddress._,
            val.workPlaceOrderOfArrival ? val.workPlaceOrderOfArrival._ : null
          );

          this.log.info('consultation', this.consultation);

          this.setMap();

        },
        (error) => this.errorMsg = 'Nenhuma consulta encontrada :(');
  }

  setMap() {
    setTimeout(() => {
      ReceiptComponent.coord = new google.maps.LatLng(this.consultation.lat, this.consultation.lng);

      const map = new google.maps.Map(document.getElementById('map'), {
        center: ReceiptComponent.coord,
        zoom: 15,
        mapTypeId: google.maps.MapTypeId.ROADMAP,
        disableDefaultUI: true,
        zoomControl: true,
      });

      const marker = new google.maps.Marker({
        position: ReceiptComponent.coord,
        icon: 'assets/icons/ic_pin_drja.svg'
      });

      marker.setMap(map);
    }, 500);


  }

  formatDate() {
    const d = moment(this.consultation.date + this.consultation.time, 'DD/MM/YYYY HH:mm');

    const date = `${d.format('dddd')}, ${d.format('D')} de ${d.format('MMMM')} - `;

    if (!this.consultation.workPlaceOrderOfArrival)
      return date + this.consultation.time;

    else {
      return `${date} a partir de ${d.format('HH')}:00`;
    }
  }


}



