import {Injectable} from '@angular/core';
import {Http} from '@angular/http';
import {Observable} from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/filter';
import {environment} from '../../environments/environment';


@Injectable()
export class JsonService {

  ROOTPATH = '../../assets/json/';

  constructor(private http: Http) {
  }

  public getSpecialities() {

    return this.http.get(this.ROOTPATH + environment.specialitiesJSON)
      .map((resp) => {
        return resp.json().filter(s => !s.parent_id);
      });

  }

  getLocalities() {

    return this.http.get(this.ROOTPATH + 'localities.json')
      .map((resp) => {
        return resp.json();
      });
  }

  getPrices() {
    return this.http.get(this.ROOTPATH + 'prices.json')
      .map((resp) => resp.json());
  }

}



