import {Component, Input, OnInit} from '@angular/core';
import {Utils} from '../../utils/utils';
import * as moment from 'moment';

@Component({
  selector: 'app-consultation-detail-card',
  templateUrl: './consultation-detail-card.component.html',
  styleUrls: ['./consultation-detail-card.component.scss']
})
export class ConsultationDetailCardComponent implements OnInit {

  @Input() user;

  constructor() { }

  ngOnInit() {
  }

  setDate() {
    const date = moment.unix(this.user.schedule.availability.timestamp);

    return `${date.format('DD')} de ${date.format('MMMM')} s ${this.user.schedule.availability.time} `;
  }

  formatWeekDay() {
    const date = moment.unix(this.user.schedule.availability.timestamp);

    return Utils.formatWeekDay(date.format('dddd'));
  }

}



