import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';

import {AppRoutingModule} from './app-routing.module';
import {AppComponent} from './app.component';
import {RequestComponent} from './pages/request/request.component';
import {AvailabilityComponent} from './pages/availability/availability.component';
import {ConfirmationComponent} from './pages/confirmation/confirmation.component';

import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {HeaderComponent} from './components/header/header.component';
import {FooterComponent} from './components/footer/footer.component';
import {FormsModule} from '@angular/forms';
import {TextMaskModule} from 'angular2-text-mask';
import {JsonService} from './services/json.service';
import {HttpModule} from '@angular/http';
import {GeocoderService} from './services/geocoder.service';
import {ApiService} from './services/api.service';
import {LocationSelectComponent} from './components/location-select/location-select.component';
import {LoaderComponent} from './components/loader/loader.component';
import {ScheduleService} from './services/schedule.service';
import {TimePipe} from './pipes/time.pipe';
import {LocationModalComponent} from './components/location-modal/location-modal.component';
import {ConfirmationModalComponent} from './components/confirmation-modal/confirmation-modal.component';
import {TrackingService} from './services/tracking.service';
import {FinalizedComponent} from './pages/finalized/finalized.component';
import {NoResultsComponent} from './pages/no-results/no-results.component';
import {ReceiptComponent} from './pages/receipt/receipt.component';
import {AzureService} from './services/azure.service';
import {MixPanelService} from './services/mix-panel.service';
import { ConfirmedLinkComponent } from './pages/confirmed-link/confirmed-link.component';
import {AuthGuardService} from './services/auth-guard.service';
import { ConsultationDetailCardComponent } from './components/consultation-detail-card/consultation-detail-card.component';



@NgModule({
  declarations: [
    AppComponent,
    RequestComponent,
    AvailabilityComponent,
    ConfirmationComponent,
    HeaderComponent,
    FooterComponent,
    LocationSelectComponent,
    LoaderComponent,
    TimePipe,
    LocationModalComponent,
    ConfirmationModalComponent,
    FinalizedComponent,
    NoResultsComponent,
    ReceiptComponent,
    ConfirmedLinkComponent,
    ConsultationDetailCardComponent
  ],
  imports: [
    // Add .withServerTransition() to support Universal rendering.
    // The application ID can be any identifier which is unique on
    // the page.
    BrowserModule.withServerTransition({appId: 'drja'}),
    AppRoutingModule,
    FormsModule,
    HttpModule,
    TextMaskModule,
    NgbModule.forRoot()
  ],
  providers: [JsonService, GeocoderService, ApiService, ScheduleService, TrackingService, AzureService, MixPanelService, AuthGuardService],
  entryComponents: [
    LocationModalComponent,
    ConfirmationModalComponent,
    ConsultationDetailCardComponent
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}



