import {Component, OnInit} from '@angular/core';
import {ScheduleService} from '../../services/schedule.service';
import {Router} from '@angular/router';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {User} from '../../models/user';
import {TrackingService} from '../../services/tracking.service';
import {AzureService} from '../../services/azure.service';
import {MixPanelService} from '../../services/mix-panel.service';

@Component({
  selector: 'app-confirmation-modal',
  templateUrl: './confirmation-modal.component.html',
  styleUrls: ['./confirmation-modal.component.scss']
})
export class ConfirmationModalComponent implements OnInit {

  user: User;

  isConfirming = false;
  error = false;

  constructor(public activeModal: NgbActiveModal,
              private router: Router,
              private mixPanelService: MixPanelService,
              private scheduleService: ScheduleService,
              private azureService: AzureService) {
  }


  ngOnInit() {
    this.user = this.scheduleService.user;
  }

  onClose() {
    if (!this.isConfirming) {
      this.activeModal.dismiss('Cross click');
    }
  }

  onSubmit() {
    this.isConfirming = true;

    this.azureService.setConfirmationLink({
      databaseID: this.user.databaseID,
      phone: this.user.phone
    })
      .subscribe(() => {

          this.mixPanelService.onConfirmModalAppointment();

          this.isConfirming = false;
          this.onClose();

          this.router.navigate(['obrigado']);
        },
        (err) => {
          this.isConfirming = false;
          this.error = true;
        });


  }


}



