import {Injectable} from '@angular/core';
import {Http} from '@angular/http';
import {environment} from '../../environments/environment';

@Injectable()
export class AzureService {

  ROOT = environment.root;

  constructor(private http: Http) {
  }


  public setRequestScheduler(obj) {
    return this.http.post(this.ROOT + '/scheduler', obj).map(resp => resp.json());
  }

  public setConfirmationLink(obj) {
    return this.http.post(this.ROOT + '/confirmationLink', obj).map(resp => resp.json());
  }

  public setConfirmed(obj) {
    return this.http.post(this.ROOT + '/confirmed', obj).map(resp => resp.json());
  }

  public getConsultation(id) {
    return this.http.get(this.ROOT + '/scheduler?id=' + id).map(resp => resp.json());
  }

}



