export class Utils {


  public static checkValidation(isValid: boolean, str: string, str2: string) {
    return isValid && str.localeCompare(str2) === 0;
  }

  public static formatWeekDay(date) {
    const i = date.indexOf('-');
    if (date && i > -1)
      return date.substring(0, i);

    return date;
  }

  public static simplifyString(str: string) {
   return this.removeAllWhiteSpace(this.removeDash(this.removeAccent(str.toLowerCase())));
  }

  public static removeAccent(inStr) {
    return inStr.replace(/([])|([])|([])|([])|([])|([])|([])|([])|([])|([])/g, function (str, a, c, e, i, n, o, s, u, y, ae) {
      if (a) return 'a'; else if (c) return 'c'; else if (e) return 'e'; else if (i) return 'i'; else if (n) return 'n'; else if (o) return 'o'; else if (s) return 's'; else if (u) return 'u'; else if (y) return 'y'; else if (ae) return 'ae';
    });
  }

  public static removeDash(str) {
    return str.replace(/-/g, '');
  }

  public static removeAllWhiteSpace(str) {
    return str.replace(/\s/g, '');
  }
}



