import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {JsonService} from '../../services/json.service';
import {GeocoderService} from '../../services/geocoder.service';
import {Utils} from '../../utils/utils';
import {ScheduleService} from '../../services/schedule.service';
import {ActivatedRoute} from '@angular/router';
import {QueryParams} from '../../models/queryParams';
import {isEmpty} from 'lodash';
import 'rxjs/add/operator/mergeMap';
import {Observable} from 'rxjs/Observable';
import {Log} from 'ng2-logger';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';


@Component({
  selector: 'app-location-select',
  templateUrl: './location-select.component.html',
  styleUrls: ['./location-select.component.scss']
})
export class LocationSelectComponent implements OnInit {

  json = {
    localities: []
  };

  log = Log.create('LocationSelectComponent');

  cities: any[];
  neighborhoods: any[];

  location = {
    city: 'Selecione a Cidade',
    neighborhood: 'Selecione o Bairro',
    state: null
  };


  @Output() coordinates: EventEmitter<any> = new EventEmitter<any>();
  @Output() locality: EventEmitter<any> = new EventEmitter<any>();
  @Input() validation: boolean;


  constructor(private jsonService: JsonService,
              private geocoderService: GeocoderService,
              private scheduleService: ScheduleService,
              private activatedRoute: ActivatedRoute) {
  }

  ngOnInit() {
    this.init();
    this.setSelectFromJSON();
  }


  setSelectFromJSON() {

    this.jsonService.getLocalities()
      .map(s => {
        this.json.localities = s;

        this.setCities(s);
      })
      .mergeMap(() => this.activatedRoute.queryParams)
      .subscribe((val: QueryParams) => {

        isEmpty(val) ? this.checkLocalStorage() : this.checkQueryParams(val);

        if (
          this.location.city.localeCompare('Selecione a Cidade') !== 0 &&
          this.location.neighborhood.localeCompare('Selecione o Bairro') !== 0)
          this.onChangeNeighborhood();
      });
  }


  checkLocalStorage() {
    const lStorage = localStorage.getItem('user');

    if (lStorage) {
      const l = JSON.parse(lStorage);

      l.locality.state = this.getState(l.locality.city);
      l.locality.city = this.getCity(l.locality.city);

      this.checkQueryParams(new QueryParams(null, l.locality.state, l.locality.city, l.locality.neighborhood));
    }
  }

  checkQueryParams(val: QueryParams) {

    if (val && val.estado) {
      const state = this.json.localities
        .find(s => {
          return Utils.simplifyString(s.id).localeCompare(Utils.simplifyString(val.estado)) === 0;
        });

      if (state) {
        this.setCities(this.json.localities);

        if (val.cidade) {
          const city = this.cities.find(s => {
            return Utils.simplifyString(this.getCity(s.name)).localeCompare(Utils.simplifyString(val.cidade)) === 0;
          });

          if (city) {
            this.neighborhoods = city.neighborhoods;
            this.location.city = city.name;

            if (val.bairro) {
              const neighborhood = city.neighborhoods.find(s => {
                return Utils.simplifyString(s.name).localeCompare(Utils.simplifyString(val.bairro)) === 0;
              });

              if (neighborhood)
                this.location.neighborhood = neighborhood.name;
            }
          }
        }
      }
    }

  }

  init() {
    this.cities = this.neighborhoods = null;
    this.coordinates.emit(null);
    this.location.city = 'Selecione a Cidade';
    this.location.neighborhood = 'Selecione o Bairro';

  }

  checkValidation(str1: string, str2: string) {
    return Utils.checkValidation(this.validation, str1, str2);
  }

  onChangeCity(value) {
    this.location.neighborhood = 'Selecione o Bairro';
    this.coordinates.emit(null);

    this.neighborhoods = this.cities.find(c => c.name.localeCompare(value) === 0).neighborhoods;
  }

  onChangeNeighborhood() {
    const address = `${this.location.neighborhood}, ${this.location.city}`;
    this.coordinates.emit(null);

    this.location.state = this.getState(this.location.city);

    this.locality.emit(this.location);

    const observer = !this.geocoderService.isGoogle ? this.geocoderService.isGoogleUp() : Observable.of('');

    observer
      .mergeMap(() => this.mapCoordinates(address))
      .subscribe(resp => {

        this.log.info('Address', address);
        this.log.info('Coord', resp);

        this.coordinates.emit(resp);
      });

  }

  private getState(str: string) {
    return str.substring(str.length - 2);
  }

  private getCity(str: string) {
    return str.substring(0, str.indexOf(','));
  }

  private mapCoordinates(address) {
    return this.geocoderService.codeAddress(address)
      .map(resp => {
        return {
          lat: resp[0].geometry.location.lat(),
          lng: resp[0].geometry.location.lng(),
        };
      });

  }

  private setCities(arr: any[]) {

    this.cities = arr.reduce((acc, r) => {
      return [...acc, ...r.cities.map(c => {
        return {name: `${c.name}, ${r.id}`, neighborhoods: c.neighborhoods};
      })];
    }, [])
      .sort((a, b) => {
        return a.name.localeCompare(b.name);
      });
  }

}



